.consent-button {
  cursor: pointer;
  border: 2px solid var(--color-fuchsia);
  padding: 0;
  background-color: transparent;
  border-radius: var(--br-11xs);
  box-sizing: border-box;
  width: 24px;
  overflow: hidden;
  flex-shrink: 0;
}
.lorem-ipsum-dolor2 {
  align-self: stretch;
  flex: 1;
  position: relative;
  display: flex;
  align-items: center;
}
.consent-para,
.consent-wrapper {
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.consent-para {
  margin: 0;
  flex: 1;
}
.consent-wrapper {
  align-self: stretch;
  flex-wrap: wrap;
  padding: var(--padding-xl);
  gap: var(--gap-3xs);
  text-align: left;
  font-size: var(--q-body-size);
  color: var(--color-white);
  font-family: var(--q-h4);
}
