.hackathon-around-you {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: inherit;
  font-weight: 400;
  font-family: inherit;
}
.beapart-title2,
.columns2,
.header2 {
  display: flex;
  align-items: center;
  justify-content: center;
}
.beapart-title2 {
  flex: 1;
  overflow: hidden;
  flex-direction: column;
}
.columns2,
.header2 {
  align-self: stretch;
  flex-direction: row;
  padding: var(--padding-5xl);
}
.columns2 {
  flex-wrap: wrap;
  padding: var(--padding-21xl);
  gap: var(--gap-xl);
}
.hackathon-around-you-home,
.heading-3-columns2 {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}
.hackathon-around-you-home {
  background-color: var(--color-gray);
  overflow: hidden;
  align-items: center;
  text-align: center;
  font-size: var(--q-h2-size);
  color: var(--color-white);
  font-family: var(--q-h4);
}
@media screen and (max-width: 960px) {
  .columns2 {
    flex-direction: column;
    gap: var(--gap-10xl);
    align-items: center;
    justify-content: center;
  }
}
@media screen and (max-width: 420px) {
  .columns2 {
    flex-direction: column;
    gap: var(--gap-11xl);
    align-items: center;
    justify-content: center;
  }
}
