.gohack-weekend-at,
.th-18th {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-weight: 400;
  font-family: inherit;
}
.gohack-weekend-at {
  font-size: var(--q-h1-size);
  background: linear-gradient(180deg, #8039b9 40.63%, #ff00c7);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.th-18th {
  font-size: inherit;
}
.content2,
.hero-hackathon-page {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.content2 {
  height: 316px;
  justify-content: center;
  gap: var(--gap-5xl);
}
.hero-hackathon-page {
  overflow: hidden;
  justify-content: flex-start;
  padding: 0 var(--padding-5xl);
  text-align: center;
  font-size: var(--q-h5-size);
  color: var(--color-white);
  font-family: var(--q-h4);
}
