.lorem-ipsum1,
.primary-button {
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: stretch;
}
.lorem-ipsum1 {
  flex: 1;
  position: relative;
  font-size: var(--q-body-size);
  font-family: var(--q-h4);
  color: var(--color-white);
  text-align: center;
}
.primary-button {
  cursor: pointer;
  border: 0;
  padding: var(--padding-8xs) var(--padding-3xs);
  background-color: var(--b9);
  border-radius: var(--br-8xs);
  height: 40px;
  overflow: hidden;
  flex-direction: row;
  box-sizing: border-box;
}
