.problem-statements {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: inherit;
  font-weight: 400;
  font-family: inherit;
}
.beapart-title1,
.header1 {
  align-items: center;
  justify-content: center;
}
.beapart-title1 {
  flex: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}
.header1 {
  flex-direction: row;
  padding: var(--padding-5xl);
}
.columns1,
.header1,
.heading-3-columns1 {
  align-self: stretch;
  display: flex;
}
.columns1 {
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding: var(--padding-21xl);
}
.heading-3-columns1 {
  flex: 1;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}
.hackathon-page,
.problem-statement-hackathon {
  background-color: var(--color-gray);
  overflow: hidden;
  display: flex;
}
.problem-statement-hackathon {
  align-self: stretch;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  text-align: center;
  font-size: var(--q-h2-size);
  color: var(--color-white);
  font-family: var(--q-h4);
}
.hackathon-page {
  position: relative;
  width: 100%;
  height: 1594.5px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media screen and (max-width: 420px) {
  .columns1 {
    flex-direction: row;
    gap: var(--gap-21xl);
    align-items: center;
    justify-content: center;
  }
}
