.about-content-child {
  position: relative;
  background-color: var(--b9);
  width: 20px;
  height: 81px;
}
.gohack-is-a {
  margin: 0;
  flex: 1;
  position: relative;
  font-size: inherit;
  font-weight: 400;
  font-family: inherit;
}
.about-content {
  flex: 1;
  flex-wrap: wrap;
  padding: var(--padding-21xl);
  gap: var(--gap-3xs);
}
.about-content,
.about-home,
.container {
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.container {
  flex: 1;
  flex-wrap: wrap;
  padding: var(--padding-21xl);
  background-image: url(/public/container@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
}
.about-home {
  align-self: stretch;
  text-align: center;
  font-size: var(--q-h3-size);
  color: var(--color-white);
  font-family: var(--q-h4);
}
.be-a-part {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: inherit;
  font-weight: 400;
  font-family: inherit;
}
.beapart-title,
.columns,
.header {
  display: flex;
  align-items: center;
  justify-content: center;
}
.beapart-title {
  flex: 1;
  overflow: hidden;
  flex-direction: column;
}
.columns,
.header {
  align-self: stretch;
  flex-direction: row;
  padding: var(--padding-5xl);
}
.columns {
  flex-wrap: wrap;
  justify-content: space-between;
  padding: var(--padding-21xl);
}
.be-a-part-home,
.heading-3-columns,
.home {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.heading-3-columns {
  align-self: stretch;
  align-items: flex-start;
}
.be-a-part-home,
.home {
  background-color: var(--color-gray);
  overflow: hidden;
  align-items: center;
}
.be-a-part-home {
  align-self: stretch;
  text-align: center;
  font-size: var(--q-h2-size);
  color: var(--color-white);
  font-family: var(--q-h4);
}
.home {
  position: relative;
  width: 100%;
}
@media screen and (max-width: 420px) {
  .about-content {
    flex-direction: row;
    gap: var(--gap-3xs);
    align-items: center;
    justify-content: center;
  }
  .container {
    flex: unset;
    align-self: stretch;
  }
  .about-home {
    flex-direction: column;
  }
  .columns {
    flex-direction: row;
    gap: var(--gap-21xl);
    align-items: center;
    justify-content: center;
  }
}
