.logo-icon {
  width: 141px;
  height: 36px;
  object-fit: cover;
}
.navbar-brand,
.navbarcontainer {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.navbarcontainer {
  flex: 1;
  align-items: center;
  justify-content: space-between;
}
.hamburger-child,
.hamburger-inner,
.hamburger-item {
  position: absolute;
  height: 24.24%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 75.76%;
  left: 0;
  border-radius: var(--br-81xl);
  background-color: var(--color-white);
}
.hamburger-inner,
.hamburger-item {
  top: 39.39%;
  bottom: 36.36%;
}
.hamburger-inner {
  height: 21.21%;
  top: 78.79%;
  bottom: 0;
}
.hamburger {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  position: relative;
  width: 40px;
  height: 33px;
  display: none;
}
.navbar-logo-left {
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 0 var(--padding-5xl);
  box-sizing: border-box;
  gap: var(--gap-3xs);
  align-self: stretch;
}
@media screen and (max-width: 960px) {
  .hamburger {
    display: flex;
  }
}
@media screen and (max-width: 420px) {
  .hamburger {
    display: flex;
  }
}
