@import url("https://fonts.googleapis.com/css2?family=Questrial:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --q-h4: Questrial;

  /* font sizes */
  --q-body-size: 16px;
  --q-h3-size: 26px;
  --q-h2-size: 30px;
  --q-h5-size: 19px;
  --q-h1-size: 34px;
  --q-h4-size: 22px;

  /* Colors */
  --color-gray: #0a050d;
  --b9: #8039b9;
  --color-white: #fff;
  --color-fuchsia: #ff00c7;

  /* Gaps */
  --gap-xl: 20px;
  --gap-10xl: 29px;
  --gap-11xl: 30px;
  --gap-21xl: 40px;
  --gap-3xs: 10px;
  --gap-5xs: 8px;
  --gap-5xl: 24px;
  --gap-606xl: 625px;
  --gap-13xl: 32px;

  /* Paddings */
  --padding-21xl: 40px;
  --padding-xl: 20px;
  --padding-8xs: 5px;
  --padding-3xs: 10px;
  --padding-5xl: 24px;
  --padding-5xs: 8px;
  --padding-xs: 12px;
  --padding-16xl: 35px;
  --padding-124xl: 143px;
  --padding-81xl: 100px;

  /* Border radiuses */
  --br-3xs: 10px;
  --br-8xs: 5px;
  --br-81xl: 100px;
  --br-11xs: 2px;
}
