.teamname-label {
  cursor: pointer;
  align-self: stretch;
  position: relative;
  font-size: var(--q-h4-size);
  font-family: var(--q-h4);
  color: var(--color-white);
  text-align: left;
}
.teamname-input {
  border: 0;
  background-color: transparent;
  align-self: stretch;
  flex: 1;
  border-bottom: 1px solid var(--color-white);
  overflow: hidden;
}
.ps-input,
.teamname-wrapper {
  align-self: stretch;
  overflow: hidden;
  display: flex;
}
.teamname-wrapper {
  height: 104.5px;
  flex-shrink: 0;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: var(--padding-xl);
  box-sizing: border-box;
}
.ps-input {
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-3xs);
  gap: var(--gap-xl);
}
.primary-button-wrapper,
.ps-wrapper,
.registration-form {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-self: stretch;
}
.ps-wrapper {
  align-items: flex-start;
  padding: var(--padding-xl);
  gap: var(--gap-3xs);
}
.primary-button-wrapper,
.registration-form {
  align-items: center;
}
.primary-button-wrapper {
  padding: var(--padding-16xl) var(--padding-124xl);
}
.registration-form {
  border-radius: var(--br-3xs);
}
