.card-2-child {
  align-self: stretch;
  flex: 1;
  position: relative;
  border-radius: var(--br-8xs);
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.find-a-hackathon,
.lorem-ipsum-dolor {
  margin: 0;
  align-self: stretch;
  position: relative;
}
.find-a-hackathon {
  font-size: inherit;
  font-weight: 400;
  font-family: inherit;
}
.lorem-ipsum-dolor {
  font-size: var(--q-body-size);
  color: var(--color-white);
}
.card-2 {
  border-radius: var(--br-3xs);
  border: 2px solid var(--b9);
  box-sizing: border-box;
  width: 404px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: var(--padding-xl);
  gap: var(--gap-xl);
  text-align: left;
  font-size: var(--q-h3-size);
  color: var(--color-fuchsia);
  font-family: var(--q-h4);
}
